e-between, $ali: center) {\r\n  display: flex;\r\n  flex-flow: $flow;\r\n  justify-content: $jus;\r\n  align-items: $ali;\r\n}\r\n\r\n@mixin text-overflow {\r\n  text-overflow: ellipsis;\r\n  overflow: hidden;\r\n  white-space: nowrap;\r\n}\r\n\r\n@mixin clearfix-after {\r\n  &:after {\r\n    display: block;\r\n    content: \"\";\r\n    clear: both;\r\n  }\r\n}\r\n\r\n@mixin content {\r\n  .content {\r\n    @content;\r\n  }\r\n}\r\n\r\n@mixin btn-size($width: auto, $height: auto, $font-size: 12px) {\r\n  width: $width;\r\n  height: $height;\r\n  font-size: $font-size;\r\n  border-radius: $height;\r\n}\r\n\r\n@mixin btn($width: auto, $height: auto, $font-size: 12px) {\r\n  @include flex($jus: center);\r\n  @include btn-size($width, $height, $font-size);\r\n  line-height: 1;\r\n  background: $color-active;\r\n  color: $color-white;\r\n  border: none;\r\n  font-size: $font-size;\r\n  cursor: pointer;\r\n  &:hover,\r\n  &:active {\r\n    background: $color-active-light;\r\n  }\r\n  &.base-line-btn {\r\n    background: $color-white;\r\n    border: 1px solid $color-active;\r\n    color: $color-active;\r\n    &:hover,\r\n    &:active {\r\n      background: $color-white;\r\n      border-color: $color-active-light;\r\n      color: $color-active-light;\r\n    }\r\n  }\r\n}\r\n\r\n@mixin upload-btn {\r\n  position: relative;\r\n  overflow: hidden;\r\n  input[type=\"file\"] {\r\n    position: absolute;\r\n    left: -100%;\r\n    top: -100%;\r\n    width: 200%;\r\n    height: 200%;\r\n    opacity: 0;\r\n    cursor: pointer;\r\n  }\r\n}\r\n\r\n@mixin auto-scroll {\r\n  overflow: auto;\r\n  &::-webkit-scrollbar {\r\n    position: absolute;\r\n    top: 10px;\r\n    padding-top: 20px;\r\n    width: 6px;\r\n    height: 6px;\r\n  }\r\n  &::-webkit-scrollbar-thumb {\r\n    /*滚动条里面小方块*/\r\n    border-radius: 10px;\r\n    background-color: $color-input-border;\r\n  }\r\n  &::-webkit-scrollbar-track {\r\n    /*滚动条里面轨道*/\r\n    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);\r\n    /