default;\r\n        .form-label {\r\n          width: 75px;\r\n          font-size: $font-default;\r\n        }\r\n        .form-input {\r\n          width: 146px;\r\n          height: 24px;\r\n          font-size: 12px;\r\n          &[disabled] {\r\n            background: $color-disabled-light;\r\n          }\r\n        }\r\n        &:nth-child(n + 2) {\r\n          margin-top: 14px;\r\n        }\r\n      }\r\n    }\r\n    .selector-box {\r\n      // min-height: 94px;\r\n      padding-left: 12px;\r\n      .selector-tips {\r\n        padding: 5px 7px;\r\n        font-size: 12px;\r\n        line-height: 1;\r\n      }\r\n      .option-list {\r\n        @include flex(row wrap, flex-start);\r\n        overflow: auto;\r\n        max-height: 68px;\r\n        .option {\r\n          margin: 7px;\r\n          width: 70px;\r\n          height: 20px;\r\n          border-radius: 10px;\r\n          border: 1px solid $color-input-border-dark;\r\n          @include flex($jus: center);\r\n          cursor: pointer;\r\n          &:hover {\r\n            background: $color-input-hover;\r\n          }\r\n          &.active {\r\n            border-color: $color-active;\r\n            background: $color-active;\r\n            color: $color-white;\r\n          }\r\n          &.disabled {\r\n            cursor: not-allowed;\r\n            border-color: $color-border;\r\n            background: $color-bg-light;\r\n            color: $color-disabled;\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n  .result-table {\r\n  }\r\n}\r\n"],"sourceRoot":""}]);

